"use client";

import { cn } from "@/utils/helper";
import { ButtonHTMLAttributes, ReactNode } from "react";

interface CommonButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: "primary" | "secondary" | "outline" | "danger";
  icon?: ReactNode;
  children: ReactNode;
}

export default function CommonButton({
  variant = "primary",
  icon,
  children,
  className,
  ...props
}: CommonButtonProps) {
  const baseStyles =
    "flex items-center justify-center gap-2 px-4 py-2 rounded-md font-medium transition-all cursor-pointer text-lg";

  const variants: Record<string, string> = {
    primary:
      "bg-(--color-btn-primary-500) hover:bg-(--color-btn-primary-600) text-(--color-bg-neutral-50) border border-(--color-btn-primary-500) hover:border-(--color-btn-primary-600)",
    secondary:
      "bg-transparent text-(--color-btn-primary-500) border border-(--color-btn-primary-500) hover:border-(--color-btn-primary-600) hover:bg-(--color-btn-teritary-100) hover:text-(--color-btn-primary-600)",
    outline:
      "border border-(--color-primary) text-(--color-primary) hover:bg-(--color-primary) hover:text-(--color-text-primary)",
    danger: "bg-red-600 text-white hover:bg-red-700",
  };

  return (
    <button className={cn(baseStyles, variants[variant], className)} {...props}>
      {icon && icon}
      {children}
    </button>
  );
}
