"use client";
import GetStartedForm from "@/components/onbording/GetStartedForm";
import OtpForm from "@/components/onbording/OtpForm";
import { motion, AnimatePresence } from "framer-motion";
import { useState } from "react";

export default function GetStartedPage() {
  const [phone, setPhone] = useState("");
  const [agreed1, setAgreed1] = useState(true);
  const [agreed2, setAgreed2] = useState(true);
  const [showOTP, setShowOTP] = useState(false);
  const [otp, setOtp] = useState(["", "", "", ""]);

  const handleOtpChange = (index: number, value: string) => {
    if (value.length > 1) return;
    const newOtp = [...otp];
    newOtp[index] = value.replace(/[^0-9]/g, "");
    setOtp(newOtp);
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: 0 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ delay: 1, duration: 0.8, ease: "easeOut" }}
      className="w--full h-full flex items-center justify-center"
    >
      <AnimatePresence mode="wait">
        {!showOTP ? (
          <GetStartedForm
            setPhone={setPhone}
            phone={phone}
            agreed1={agreed1}
            setAgreed1={setAgreed1}
            agreed2={agreed2}
            setAgreed2={setAgreed2}
            setShowOTP={setShowOTP}
          />
          
        ) : (
          <OtpForm phone={phone} otp={otp} handleOtpChange={handleOtpChange} />
        )}
      </AnimatePresence>
    </motion.div>
  );
}
